import config from 'config';
import {
	authHeader
} from '../_helpers';

export const userService = {
	getItemList,
	login,
	logout,
	register,
	getAll,
	getById,
	buy,
	delete: _delete
};

function getItemList() {
	const requestOptions = {
		method: 'GET',
		headers: authHeader()
	};
	return fetch(`${config.apiUrl}/item/list`, requestOptions).then(handleResponse);
}

function login(userName, password) {
	const requestOptions = {
		method: 'POST',
		headers: {
			'Content-Type': 'application/json'
		},
		body: JSON.stringify({
			userName,
			password
		})
	};

	return fetch(`${config.apiUrl}/user/authenticate`, requestOptions)
		.then(handleResponse)
		.then(user => {
			// login successful if there's a jwt token in the response
			if (user.jsonWebToken) {
				// store user details and jwt token in local storage to keep user logged in between page refreshes
				localStorage.setItem('user', JSON.stringify(user));
			}

			return user;
		});
}

function logout() {
	// remove user from local storage to log user out
	localStorage.removeItem('user');
}

function register(user) {
	const requestOptions = {
		method: 'POST',
		headers: {
			'Content-Type': 'application/json',
			'Access-Control-Allow-Origin': '*'
		},
		body: JSON.stringify(user)
	};
	console.log(user);

	return fetch(`${config.apiUrl}/user/register`, requestOptions).then(handleResponse);
}

function getAll() {
	const requestOptions = {
		method: 'GET',
		headers: authHeader()
	};

	return fetch(`${config.apiUrl}user`, requestOptions).then(handleResponse);
}

function getById(id) {
	const requestOptions = {
		method: 'GET',
		headers: authHeader()
	};

	return fetch(`${config.apiUrl}/user/${id}`, requestOptions).then(handleResponse);
}

function buy(info) {
	const requestOptions = {
		method: 'PUT',
		headers: { ...authHeader(),
			'Content-Type': 'application/json'
		},
	};
	const user = localStorage.getItem('user');

	console.log(info);
	return fetch(`${config.apiUrl}/user/${user.id}/buy/${info.id}`, requestOptions).then(handleResponse);
}

// prefixed function name with underscore because delete is a reserved word in javascript
function _delete(id) {
	const requestOptions = {
		method: 'DELETE',
		headers: authHeader()
	};

	return fetch(`${config.apiUrl}/user/${id}`, requestOptions).then(handleResponse);
}

function handleResponse(response) {
	return response.text().then(text => {
		console.log(text)
		var data = '';
		try {
			data = JSON.parse(text)
		} catch (e) {
			data = text
		}
		
		if (data == "RegisterFailed") {
			const error = '注册失败';
			return Promise.reject(error);
		}
		else if(data == "Wrong Pwd or account"){
			const error = '用户名或密码错误';
			return Promise.reject(error);
		}
		else{
			return data;
		}

		
	});
}
