<template>
	<div>
		<h2>注册</h2>
		<form @submit.prevent="handleSubmit">
			<div class="form-group">
				<label for="name">真实姓名</label>
				<input type="text" v-model="user.name" v-validate="'required'" name="name" class="form-control" :class="{ 'is-invalid': submitted && errors.has('name') }" />
				<div v-if="submitted && errors.has('name')" class="invalid-feedback">{{ errors.first('name') }}</div>
			</div>
			<div class="form-group">
				<label for="stuid">学号</label>
				<input type="text" v-model="user.stuId" v-validate="'required'" name="stuid" class="form-control" :class="{ 'is-invalid': submitted && errors.has('stuid') }" />
				<div v-if="submitted && errors.has('stuid')" class="invalid-feedback">{{ errors.first('stuid') }}</div>
			</div>
			<div class="form-group">
				<label for="phoneNumber">手机号</label>
				<input type="text" v-model="user.phoneNumber" v-validate="'required'" name="phoneNumber" class="form-control"
				 :class="{ 'is-invalid': submitted && errors.has('phoneNumber') }" />
				<div v-if="submitted && errors.has('phoneNumber')" class="invalid-feedback">{{ errors.first('phoneNumber') }}</div>
			</div>
			<div class="form-group">
				<label for="userName">用户名</label>
				<input type="text" v-model="user.userName" v-validate="'required'" name="userName" class="form-control" :class="{ 'is-invalid': submitted && errors.has('userName') }" />
				<div v-if="submitted && errors.has('userName')" class="invalid-feedback">{{ errors.first('userName') }}</div>
			</div>
			<div class="form-group">
				<label htmlFor="password">密码</label>
				<input type="password" v-model="user.password" v-validate="{ required: true, min: 6 }" name="password" class="form-control"
				 :class="{ 'is-invalid': submitted && errors.has('password') }" />
				<div v-if="submitted && errors.has('password')" class="invalid-feedback">{{ errors.first('password') }}</div>
			</div>
			<div class="form-group">
				<label for="intro">自我介绍</label>
				<input type="text" v-model="user.intro" v-validate="'required'" name="intro" class="form-control" :class="{ 'is-invalid': submitted && errors.has('intro') }" />
				<div v-if="submitted && errors.has('intro')" class="invalid-feedback">{{ errors.first('intro') }}</div>
			</div>
			<div class="form-group">
				<button class="btn btn-primary" :disabled="status.registering">注册</button>
				<img v-show="status.registering" src="data:image/gif;base64," />
				<router-link to="/login" class="btn btn-link">返回到登录</router-link>
			</div>
		</form>
	</div>
</template>

<script>
	import {
		mapState,
		mapActions
	} from 'vuex'

	export default {
		data() {
			return {
				user: {
					name: '',
					phoneNumber: '',
					userName: '',
					password: '',
					intro: '',
					stuId: ''
				},
				submitted: false
			}
		},
		computed: {
			...mapState('account', ['status'])
		},
		methods: {
			...mapActions('account', ['register']),
			handleSubmit(e) {
				this.submitted = true;
				this.$validator.validate().then(valid => {
					if (valid) {
						this.register(this.user);
					}
				});
			}
		}
	};
</script>

<style>
	body {
		background-color: #f5f5f5;
		display: flex;
		padding-top: 40px;
	}
</style>
