import { authHeader } from '../_helpers';
import config from 'config';

function handleResponse(response) {
    return response.text().then(text => {
        const data = text;

        return data;
    });
}

export default function fetchItem (limit, skip) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };
  return fetch(`${config.apiUrl}/item/list`, requestOptions).then(handleResponse)
      .then(
          lists => {
              const l = JSON.parse(lists);
              return {
                  list: l,
                  count: l.length
              }
          });
}
