    color: #ff6000;
}
</style>
<template>
    <li class="mi-item">
        <a class="version-item">
            <div class="version-item-img">
                <img class="lazy" :src="data.imgUrl">
            </div>
            <div class="version-item-intro">
                <div class="version-item-name">
                    <p>{{data.name}}</p>
                </div>
                <div class="version-item-brief">
                    <p>{{ data.productComment }}</p>
                </div>
                <div class="version-item-intro-price">
                    <span>出售人:{{ data.sellerId }}</span>
                </div>
                <div class="version-item-intro-price">
                    <span>价格:{{ data.price }}</span>
                </div>
                <div>
                    <button @click='buyItem' class="btn btn-primary" :disabled="loading_buy">添加购物车</button>
                </div>

                <div class="version-item-brief">
                </div>
            </div>
        </a>
    </li>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { authHeader } from '../_helpers';
import config from 'config';

function handleResponse(response) {
    return response.text().then(text => {
        const data = text && JSON.parse(text);
        console.log('response', response);
        if (!response.ok) {
            if (response.status === 401) {
                // auto logout if 401 response returned from api
                logout();
                location.reload(true);
            }

            const error = (data && data.message) || response.statusText;
            return Promise.reject(error);
        }

        return data;
    });
}

export default {
  data() {
      return {
          loading_buy: false
      }
  },
  props: {
    data: Object
  },
  methods: {
      buyItem(e){
          this.loading_buy = true;
          
          const requestOptions = {
              method: 'POST',
              headers: { ...authHeader(), 'Content-Type': 'application/json' },
              body: JSON.stringify({ itemId: this.data.id })
          };
          
          const userInfo = JSON.parse(localStorage.getItem('user'));
          const that = this;
          
          fetch(`${config.apiUrl}/user/buy`, requestOptions).then(handleResponse)
            .then(
                response => {
                    that.loading_buy = false;
                    alert('已添加到购物车');
                }
            );
      }
  }
}
</script>
