<template>
	<form class="form-signin text-center" @submit.prevent="handleSubmit">
		<img class="mb-4" src="//cloud.dispnt.com/archive/imgs/logo.png" width="120" height="100">
		<h1 class="h3 mb-3 font-weight-normal">登录</h1>

		<label for="userName" class="sr-only">用户名</label>
		<input type="text" v-model="userName" name="userName" class="form-control" :class="{ 'is-invalid': submitted && !userName }"
		 placeholder="userName" required autofocus />
		<div v-show="submitted && !userName" class="invalid-feedback">请输入用户名</div>


		<label for="inputPassword" class="sr-only">密码</label>

		<input type="password" v-model="password" id="inputPassword" name="password" class="form-control" :class="{ 'is-invalid': submitted && !password }"
		 placeholder="Password" required />
		<div v-show="submitted && !password" class="invalid-feedback">请输入密码</div>

		<!-- <input type="password" id="inputPassword" class="form-control" placeholder="Password" required> -->

		<div class="checkbox mb-3">
			<label>
				<input type="checkbox" value="remember-me"> 记住我
			</label>
		</div>
		<button class="btn btn-lg btn-primary btn-block" type="submit" :disabled="status.loggingIn">登录</button>
		<router-link to="/register" class="btn btn-link">注册</router-link>
	</form>

</template>

<script>
	// import logoImg from "../assets/logo.png"
	import {
		mapState,
		mapActions
	} from 'vuex'
	export default {
		data() {
			return {
				userName: '',
				password: '',
				submitted: false
			}
		},
		computed: {
			...mapState('account', ['status'])
		},
		created() {
			//重置登陆状态

			this.logout();
		},
		methods: {
			...mapActions('account', ['login', 'logout', 'buy']),
			handleSubmit(e) {
				this.submitted = true;
				const {
					userName,
					password
				} = this;
				if (userName && password) {
					this.login({
						userName,
						password
					})
				}
			}
		}
	};
</script>

<style>
	body {
		background-color: #f5f5f5;
		display: flex;
		padding-top: 40px;
	}

	.form-signin {
		width: 100%;
		max-width: 330px;
		padding: 15px;
		margin: auto;
	}

	.form-signin .checkbox {
		font-weight: 400;
	}

	.form-signin .form-control {
		position: relative;
		box-sizing: border-box;
		height: auto;
		padding: 10px;
		font-size: 16px;
	}

	.form-signin .form-control:focus {
		z-index: 2;
	}

	.form-signin input[type="email"] {
		margin-bottom: -1px;
		border-bottom-right-radius: 0;
		border-bottom-left-radius: 0;
	}

	.form-signin input[type="password"] {
		margin-bottom: 10px;
		border-top-left-radius: 0;
		border-top-right-radius: 0;
	}
</style>
