<template>
    <div>
        <h1>欢迎{{account.user.userName}}!</h1>
        <p>{{account.user.intro}}</p>
        <span v-if="users.error" class="text-danger">ERROR: {{users.error}}</span>
        <p>
            <router-link to="/login">登出</router-link>
        </p>
        <div>
            没买东西？ => <router-link to="/shopping">购物页面</router-link>
        </div>
        <p>{{ account.user.userName }} 的已购清单</p>
        <em v-if="users.loading">Loading images...</em>
        <RecyclerView
            :prerender="30"
            style="height: 400px"
            :remain="10"
            :fetch="MiFetch"
            :item="MiItem"
            :tombstone="MiTomstone"
        ></RecyclerView>
    </div>
</template>

<script>
import MiItem from './Item.vue'
import MiTomstone from './Tombstone.vue'
import MiFetch from './mi-fetch.js'

import { mapState, mapActions } from 'vuex'

export default {
    computed: {
        ...mapState({
            account: state => state.account,
            users: state => state.users.all
        })
    },
    created () {
        this.getAllUsers();
        this.getItemList();
    },
    methods: {
        ...mapActions('users', {
            getAllUsers: 'getAll',
            deleteUser: 'delete',
            getItemList: 'getItemList'
        })
    },
    name: 'app',
    data () {
      return {
        MiFetch,
        MiItem,
        MiTomstone
      }
    }
};
</script>
