import { userService } from '../_services';
import { router } from '../_helpers';

const user = JSON.parse(localStorage.getItem('user'));
const state = user
    ? { status: { loggedIn: true }, user }
    : { status: {}, user: null};

const actions = {
    login({ dispatch, commit }, { userName, password }) {
        commit('loginRequest', { userName });

        userService.login(userName, password)
            .then(
                user => {
                    console.log(user);
                    commit('loginSuccess', user);
                    router.push('/');
                },
                error => {
                    commit('loginFailure', error);
                    dispatch('alert/error', error, { root: true });
                }
            );
    },
    logout({ commit }) {
        userService.logout();
        commit('logout');
    },
    register({ dispatch, commit }, user) {
        commit('registerRequest', user);

        userService.register(user)
            .then(
                user => {
                    commit('registerSuccess', user);
                    router.push('/login');
                    setTimeout(() => {
                        // display success message after route change completes
                        dispatch('alert/success', '注册成功', { root: true });
                    })
                },
                error => {
                    commit('registerFailure', error);
                    dispatch('alert/error', error, { root: true });
                }
            );
    },
    buy({commit}, info) {
        console.log("actions buy", info);
        commit('buyRequest', info);

        userService.buy(info)
                .then(
                    user => {
                        commit('buySuccess', info);
                    },
                    error => {
                        commit('buyFailure', error);
                    }
                );
    }
};

const mutations = {
    buyRequest(state, info) {
        console.log('mutations: request');
    },
    buySuccess(state, info) {
        console.log('mutations: Success');
    },
    buyFailure(state) {
        console.log('mutations: Failure');
    },
    loginRequest(state, user) {
        state.status = { loggingIn: true };
        state.user = user;
    },
    loginSuccess(state, user) {
        state.status = { loggedIn: true };
        state.user = user;
    },
    loginFailure(state) {
        state.status = {};
        state.user = null;
    },
    logout(state) {
        state.status = {};
        state.user = null;
    },
    registerRequest(state, user) {
        state.status = { registering: true };
    },
    registerSuccess(state, user) {
        state.status = {};
    },
    registerFailure(state, error) {
        state.status = {};
    }
};

export const account = {
    namespaced: true,
    state,
    actions,
    mutations
};
