<template>
	<div class="container">
		<!-- <div v-if="alert.message" :class="`alert ${alert.type}`">{{alert.message}}</div> -->
		
		<div v-if="alert.message" style="position: absolute; top: 0; right: 0; width: 400px; margin: 20px;" :class="`alert ${alert.type}`" class="alert-dismissible fade show" role="alert">
		  {{alert.message}}
		  <button type="button" class="close" data-dismiss="alert" v-on:click="close"aria-label="Close">
		    <span aria-hidden="true">&times;</span>
		  </button>
		</div>
		
<!-- 		<div style="position: absolute; top: 0; right: 0; width: 400px; margin: 20px;" v-if="alert.message" :class="`alert ${alert.type}`" class="toast fade show" role="alert" aria-live="assertive" aria-atomic="true">
		  <div class="toast-header">
		    <strong class="mr-auto">提示</strong>
		    <button type="button" class="ml-2 mb-1 close" data-dismiss="toast" aria-label="Close">
		      <span aria-hidden="true">×</span>
		    </button>
		  </div>
		  <div class="toast-body">
		    {{alert.message}}
		  </div>
		</div> -->
		
		<router-view></router-view>
	</div>
</template>

<script>
	import {
		mapState,
		mapActions
	} from 'vuex'

	export default {
		name: 'app',
		computed: {
			...mapState({
				alert: state => state.alert
			})
		},
		methods: {
			...mapActions({
				clearAlert: 'alert/clear'
			}),
			
			close:function(e){
				this.clearAlert();
			}
		},
		watch: {
			$route(to, from) {
				// clear alert on location change
				this.clearAlert();
			}
		}
	};
</script>


